import React from 'react';
import styles from './Card.module.css'
import { IoCloseCircleOutline } from "react-icons/io5"

export default function Card(props) {
  const { name, img, max, min, onClose, primary } = props
  // acá va tu código
  return (
    <div className={`${styles.card} ${primary ? styles.primary : ""}`}>
      <span className={styles.name}>{name}
        {!primary && <button className={styles.button} onClick={onClose}><IoCloseCircleOutline /></button>}
      </span>
      <img src={`http://openweathermap.org/img/wn/${props.img}@2x.png`} alt='Img NOT Found' />
      <div className={styles.temps}>
        <Temp Label={'Min'} temp={min} />
        <Temp Label={'Max'} temp={max} />
      </div>
    </div>
  );
};

//MALA PRACTICA, deberia estar en un archivo diferente
function Temp({ Label, temp }) {
  return (
    <div className={styles.temp}>
      <span>{Label}</span>
      <span>{temp}°</span>
    </div>
  )
}



